.bols{
    position:absolute;
    
    left:50%;
    transform:translate(-50%,-50%);
    margin:0;
    padding:0;
    display:flex;
    
  }
  .bols li{
    list-style:none;
    width:40px;
    height:40px;
    background:#fff;
    border-radius:50%;
    animation:grow 1.6s ease-in-out infinite;
  }
    @keyframes grow
    {
      0% , 40% , 100%
      {
        transform:scale(0.2);
      }
      20%{
         transform:scale(1);
  
      }
    }
  
  .bols li:nth-child(1){
    animation-delay:-1.4s;
    background:#ffff00;
    box-shadow:0 0 50px #ffff00;
    
  }
  .bols li:nth-child(2){
    animation-delay:-1.2s;
    background:#76ff03;
    box-shadow:0 0 50px #76ff03;
    
  }
  .bols li:nth-child(3){
    animation-delay:-1s;
    background:#f06292;
    box-shadow:0 0 50px #f06292;
    
  }
  .bols li:nth-child(4){
    animation-delay:-0.8s;
    background:#4fc3f7;
    box-shadow:0 0 50px #4fc3f7;
    
  }
  .bols li:nth-child(5){
    animation-delay:-0.6s;
    background:#ba68c8;
    box-shadow:0 0 50px #ba68c8;
    
  }
  .bols li:nth-child(6){
    animation-delay:-0.4s;
    background:#f57c00;
    box-shadow:0 0 50px #f57c00;
    
  }
  .bols li:nth-child(7){
    animation-delay:-0.2s;
    background:#673ab7;
    box-shadow:0 0 50px #673ab7;
    
  }

.container__progressbars {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    min-width: 270px;
    width: 100%;
    min-height: 100%;
}
.progressbar {
    position: relative;
    width: 170px;
    height: 170px;
    margin: 1em;
    transform: rotate(-90deg);
}
.progressbar__svg {
    position: relative;
    width: 100%;
    height: 100%;
}
.progressbar__svg-circle {
    width: 100%;
    height: 100%;
    fill: none;
    stroke-width: 10;
    stroke-dasharray: 440;
    stroke-dashoffset: 440;
    stroke: #fff;
    stroke-linecap: round;
    transform: translate(5px, 5px);
}
.shadow-html {
    filter: drop-shadow(0 0 5px #f63);
}
.shadow-css {
    filter: drop-shadow(0 0 5px #3bf);
}
.shadow-scss {
    filter: drop-shadow(0 0 5px #f3f);
}
.shadow-js {
    filter: drop-shadow(0 0 5px #ff3);
}
.shadow-ts {
    filter: drop-shadow(0 0 5px #37f);
}
.shadow-node {
    filter: drop-shadow(0 0 5px #5f3);
}
.shadow-react {
    filter: drop-shadow(0 0 5px #3ff);
}
.shadow-angular {
    filter: drop-shadow(0 0 5px #f33);
}
.shadow-vue {
    filter: drop-shadow(0 0 5px #3f8);
}
.circle-html {
    animation: anim_circle-html 1s ease-in-out forwards;
}
.circle-css {
    animation: anim_circle-css 1s ease-in-out forwards;
}
.circle-scss {
    animation: anim_circle-scss 1s ease-in-out forwards;
}
.circle-js {
    animation: anim_circle-js 1s ease-in-out forwards;
}
.circle-ts {
    animation: anim_circle-ts 1s ease-in-out forwards;
}
.circle-node {
    animation: anim_circle-node 1s ease-in-out forwards;
}
.circle-react {
    animation:anim_circle-html 1s ease-in-out forwards;
}
.circle-angular {
    animation: anim_circle-angular 1s ease-in-out forwards;
}
.circle-vue {
    animation: anim_circle-vue 1s ease-in-out forwards;
}
.progressbar__text {
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 0.25em 0.5em;
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
    border-radius: 0.25em;
    transform: translate(-50%, -50%) rotate(90deg);
}
@keyframes anim_circle-html {
    to {
        stroke-dashoffset: 22;
   }
}
@keyframes anim_circle-css {
    to {
        stroke-dashoffset: 20;
   }
}
@keyframes anim_circle-scss {
    to {
        stroke-dashoffset: 30;
   }
}
@keyframes anim_circle-js {
    to {
        stroke-dashoffset: 88;
   }
}
@keyframes anim_circle-ts {
    to {
        stroke-dashoffset: 98;
   }
}
@keyframes anim_circle-node {
    to {
        stroke-dashoffset: 66;
   }
}
@keyframes anim_circle-react {
    to {
        stroke-dashoffset: 22;
   }
}
@keyframes anim_circle-angular {
    to {
        stroke-dashoffset: 99;
   }
}
@keyframes anim_circle-vue {
    to {
        stroke-dashoffset: 66;
   }
}
